
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: {},
  setup(props, context) {
    let isOpen = ref(false);

    function onClick(): void {
      isOpen.value = !isOpen.value;
      context.emit("hamburger-changed", isOpen.value);
    }
    return {
      isOpen,
      onClick,
    };
  },
});
